.sign-in-button {
    display: block;
    width: 100%;
    padding: 8px;
    font-size: 1.1rem;
    font-weight: bold;
    margin-top: 1rem;
    border-color: #00bc77;
    background-color: #00bc77;
    color: #fff;
}

.sign-in-content {
    box-sizing: border-box;
    background-color: white;
    width: 300px;
    margin: 0 auto;
    margin-top: 3rem;
    padding: 2rem;
    form {
        position: relative;
    }
}


.input-remember {
    display: flex;
    label {
        margin-left: 0.25rem;
    }
}

.error {
    position: absolute;
    display: flex;
    bottom: 3.2em;
    width: 100%;
    justify-content: center;
    color: red;
    font-size: 0.8rem;
    margin-top: 0.5rem;
}